body {
  background-color: honeydew;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 3vh;
}

.head {
  color: blueviolet;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 5.3vw;
  font-style: italic;
  margin-left: 4vw;
  width: fit-content;
  background-color: #8ec5fc;
  background-image: linear-gradient(
    62deg,
    #8ec5fc 0%,
    #e0c3fc 50%,
    #ffffff 100%
  );
  padding: 3%;
  border-radius: 15%;
}

.form {
  display: flex;
  justify-content: center;
  margin: 3%;
}

.button {
  margin-left: 43%;
  margin-bottom: 2%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.7vw;
  font-weight: bold;
  transition: 0.3s ease-in-out;
}

.button:hover {
  background-color: salmon;
  color: aliceblue;
  padding: 1%;
  border-radius: 3vw;
}

.meme {
  position: relative;
  width: 90%;
  margin: auto;
}

.meme > img {
  width: 100%;
}

.meme > h3 {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: 2.8em;
  text-transform: uppercase;
  color: whitesmoke;
  letter-spacing: 1px;
}

.meme > .bottom {
  bottom: 0;
}
.meme > .top {
  top: 0;
}

.footer {
  margin: auto;
}
.footer > p {
  text-align: center;
  font-size: 1vw;
}
